<template>
	<div>
		<el-link :underline="false"
						@click="dialogTableVisible = true,
						openconsigneeInfo()" target="_blank" style="font-size: 13px;">收货信息</el-link>
		<el-dialog title="收货地址" :visible.sync="dialogTableVisible">
				<div style="width: 450px; margin: 0 auto;" >
						<el-form label-width="80px" label="left">
							
						<el-form-item label="收货地址" >
						<el-input v-model="consigneeInfo.consigneeaddress"></el-input>
						</el-form-item>
						
					<el-form-item label="收货人" >
					<el-input v-model="consigneeInfo.consigneename"></el-input>
					</el-form-item>
						
						<el-form-item label="手机号" >
						<el-input v-model="consigneeInfo.consigneephone"></el-input>
						</el-form-item>
						
						<el-button class="col-4" type="danger" @click="updateConsigneeInfo">
							保存收货人信息
						</el-button>
						</el-form>
				</div>
		</el-dialog>
		
	</div>
</template>

<script>
	import {Ajax} from "../assets/js/ajax.js"
	export default {
		name:"Consignee",
		data:function(){
			return{
					dialogTableVisible:false,
					consigneeInfo:[]
			}
		},
		props:{
			userId:Number
		},
		methods:{
			//打开收货信息
			openconsigneeInfo:function(){
				var data="uid="+parseInt(this.userId);
				Ajax("post","consigneeInfo/queryConsigneeInfouid",data,(data)=>{
					this.consigneeInfo=data
				}) 
			},
			//保存新的收货信息
			updateConsigneeInfo:function(){
				var data="id="+parseInt(this.consigneeInfo.id)+"&consigneeaddress="+this.consigneeInfo.consigneeaddress
				+"&consigneename="+this.consigneeInfo.consigneename
				+"&consigneephone="+this.consigneeInfo.consigneephone;
				Ajax("post","consigneeInfo/updateConsigneelnfo",data,(data)=>{
				if(data.success==true){
					this.$message({
						showClose: true,
						message:data.message,
						type: 'success'
						});
				}else{
					this.$message({
          showClose: true,
          message:data.message,
          type: 'error'
					});
			}
				}) 
			}
		}
	}
</script>
