/**
 * ajax抽方法
 */
function a(){
	alert("1qqwew23123")
}
function Ajax(method,url,data,success) {
	//处理浏览器兼容性
	var xhr;
	xhr = new XMLHttpRequest();
	/* if (window.XMLHttpRequest) {
		xhr = new XMLHttpRequest();
	} else {
		xhr = new ActiveXObject("Microsoft.XMLHttp");
	} */

	xhr.onreadystatechange = function() {
		if (xhr.readyState == 4 && xhr.status == 200) {
			var dataString =  xhr.responseText;//转成字符串
			if(dataString==""){
			success(dataString);
			return;
			}
			var dataObject = JSON.parse(dataString);//转成对象
			console.log(dataObject);
			success(dataObject);
		}
	}

	if (method == "post") {
		xhr.open(method, url);//打开连接	
		xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
		xhr.send(data);//发送请求
	}

	if (method == "get") {
		xhr.open(method, url);//打开连接
		xhr.send(null);//发送请求
	}
}

//返回字符串类型的ajax
function AjaxReturnString(method,url,data,success) {
	//处理浏览器兼容性
	var xhr;
	xhr = new XMLHttpRequest();
	/* if (window.XMLHttpRequest) {
		xhr = new XMLHttpRequest();
	} else {
		xhr = new ActiveXObject("Microsoft.XMLHttp");
	} */

	xhr.onreadystatechange = function() {
		if (xhr.readyState == 4 && xhr.status == 200) {
			let dataString =  xhr.responseText;//转成字符串
			success(dataString);
		}
	}

	if (method == "post") {
		xhr.open(method, url);//打开连接	
		xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
		xhr.send(data);//发送请求
	}

	if (method == "get") {
		xhr.open(method, url);//打开连接
		xhr.send(null);//发送请求
	}
}
 
export{
	Ajax,
	AjaxReturnString,
	a
}
