			<p class="atitleclass h5">{{title}}-<span style="color: #FF0000; ">{{twotitle}}</span></p>				
	<el-container style="height: 270px;">
  <el-aside width="140" class="commoditytitle" style="border-radius: 10px;" >
    <el-menu style="border:1px solid white; width: 141px;">
		<el-menu-item :index="index" @click="titleid(item.id),twotitlupdate(item.productclassiftitletwo)" 
		v-for="(item,index) in msg" :key="item" >
        <span slot="title" style="color: #000000;">{{item.productclassiftitletwo}}</span>
      </el-menu-item>
    </el-menu>
  </el-aside>		
</el-container>
	</div>
</template>

<script>
/* import {Ajax} from '../../assets/js/ajax.js' */ 
import bus from '../../main.js'
/* import Productlist from './Productlist.vue' */
	export default{
		name:'Goodsclassify',
		/* components:{Productlist}, */
		props:{
			msg:Array,
			title:String,
		},
		data:function(){
			return{
				twotitle:this.msg[0].productclassiftitletwo,
				isCollapse: true
			}
		},
		created:function(){
			bus.$on("titleTwoindex",(value)=>{
				this.twotitle=this.msg[value].productclassiftitletwo
			})
		},
		methods:{
			titleid:function(index){
				bus.$emit('eventTitleTwoid',index)//发送到商品列表
			},
			twotitlupdate:function(value){
				this.twotitle=value
			}
		}
	}
</script>

