<!-- 主页视图 -->
<template>
	<div>
	<!-- 导航栏 -->
	<Navigation></Navigation>					
	<div class="container">
		<!-- 搜索栏组件 -->
	<SearchboxComponents></SearchboxComponents>
	
	<el-carousel class="phoneslideshow" height="160px">
		<el-carousel-item>
			<img src="../assets/imgs/ca_2.jpg"/>
		</el-carousel-item><el-carousel-item>
			<img src="../assets/imgs/logo.png"/>
		</el-carousel-item>
		</el-carousel>
		
	<el-tabs @tab-click="a">
<el-tab-pane class="animate__animated animate__fadeInDownBig" v-for="obj in productTitleOne" 
:label="obj.productclassiftitle" :key="obj">
<Productinfo :oneId="obj.id" :title="obj.productclassiftitle" ></Productinfo>
</el-tab-pane>
</el-tabs>

<!-- 好货 -->
<Goodgoods></Goodgoods>
<br><br><br>

	</div>
	</div>
</template>

<script>
	import Goodgoods from '../components/commodity/Goodgoods.vue'
	import Productinfo from '../components/commodity/Productinfo.vue'
	import SearchboxComponents from '../components/SearchboxComponents.vue'
	import Navigation from '../components/Navigation.vue'
	import{Ajax} from '../assets/js/ajax.js'
	import bus from '../main.js'
	export default {
		name:'Clothing',
		components:{
			Productinfo,
			SearchboxComponents,
			Navigation,
			Goodgoods
		},
		data:function(){
			return{
					productTitleOne:[
						{"productclassiftitle":"服装","routeTo":"/clothing"},
						{"productclassiftitle":"食品","routeTo":"/food"},
						{"productclassiftitle":"家用电器","routeTo":"/appliance"},
						{"productclassiftitle":"数码产品","routeTo":"/numericalcode"},
						{"productclassiftitle":"年货节","routeTo":"/yeargoods"}
						],
			}
		},
		created:function(){
			Ajax("get","productClassifyOne/queryAllProductClassifyOne",null,(data)=>{
				this.productTitleOne=data
			})
		},
		methods:{
			a:function(el){//发送到列表组件
				var data="productclassifyoneid="+parseInt(this.productTitleOne[el.index].id);
				Ajax("post","productClassifyTwo/queryProductTwoByproductOneId",data,(data)=>{
					bus.$emit('HomePageTwoId',data[0].id)
				}) 
				bus.$emit('titleTwoindex',0);
			}
		}
	}
</script>

