
<!-- 产品列表 -->
<template>
	
<div class="d-flex justify-content-around  commodity">
		<div class="card productlistdiv shadow" v-for="v in productmsg" 
					:key="v" @click="commoditydetailed(v.id)">	
		<img :src="require('@/assets/'+v.commoditypicture)" 
					class="mt-2" style="" >
		<div class="border-bottom mt-1" style="font-size: 12px;">
		{{v.commodityname.length>9?v.commodityname.substring(0,9)+'...':v.commodityname}}
		</div>	
		<div class="d-flex justify-content-around">
			<div style="color: #FF0000; font-weight: bold;" >￥{{v.commodityprice}}</div>
			<div @click.stop="shoppingtrolley" class="el-icon-shopping-cart-2 mt-1" v-bind:title="titlehaver"></div>
			</div>
		</div>
			
		</div>
</template>

<script>
	import bus from '../../main.js'
	import {Ajax} from '../../assets/js/ajax.js'
	export default {
		name:'Productlist',
		data:function(){
			return{
				productmsg:[],
				titlehaver:"点击加入购物车"
				/* titleTwoId:0 */

			}
		},
		created:function(){
	for(let i=0;i<9;i++){
				this.productmsg.push({"id":i,"commodityname":"夏季连衣裙"+i+"",
															"commodityprice":"12"+i+"",
															"commoditypicture":'imgs/ca_2.jpg'})
			}
			var dataOne="productclassifytwoid="+parseInt(1);
			Ajax("post","commodity/queryproductclassifytwoid",dataOne,(dataOne)=>{
				this.productmsg=dataOne
			})
			
			bus.$on("HomePageTwoId",(value)=>{
				var data="productclassifytwoid="+parseInt(value);
				Ajax("post","commodity/queryproductclassifytwoid",data,(data)=>{
					this.productmsg=data
				})
			})
			
			bus.$on("eventTitleTwoid", (value) => {
			/* 	this.titleTwoId=value; */
				var data="productclassifytwoid="+parseInt(value);
				Ajax("post","commodity/queryproductclassifytwoid",data,(data)=>{
					this.productmsg=data
				})
			})
		},
		methods:{
			shoppingtrolley:function(){
				this.$message({
					showClose:true,
          message: '你的宝贝已成功坐上了你的购物车！',
          type: 'success'
        });
			},
			commoditydetailed:function(productId){
				//进入商品详情页
				this.$router.push({
						name:'CommoditydetailedPage',
						params:{id:productId}
				})
			}
		}
	}
</script>

<style>
</style>
