<template>
	<div class="d-flex justify-content-around mt-4">
		<div class="col goodgoods_underline" >
		<div class="d-flex justify-content-center align-items-center" 
				style="background-color: #C6E2FF;height: 50px; cursor: pointer;">
				<span style="font-size: 25px; font-weight:bold;">
					<i class="el-icon-sunny"></i>&nbsp;有好货&nbsp;<i class="el-icon-sunny"></i>
				</span>
		</div>
	<el-carousel :interval="3000" height="235px" class="bigscreen">
		<el-carousel-item v-for="obj in commoditydata" :key="obj" style="background-color:white;"
											class="d-flex justify-content-around align-items-center ">
			<img v-for="v in obj " :key="v" :src="require('@/assets/'+v.commoditypicture)" style="cursor: pointer;" 
			@click="commoditydetailed(v.id)" width="190px" height="190px" />
		</el-carousel-item>
	</el-carousel>
	
	<!-- 手机样式有好货 -->
	<div class="phone_haohuo">
		<div class="d-flex justify-content-between " style="flex-wrap: wrap;">
		<template v-for="obj in commoditydata">
			<div class="mt-2" v-for="v in obj " :key="v">
				<img :src="require('@/assets/'+v.commoditypicture)" 
				style="border-radius: 10px; width: 150px;" @click="commoditydetailed(v.id)" >
			</div>
			</template>
	</div>
</div>
	
		</div>
	</div>
</template>

<script>
	import {Ajax} from '../../assets/js/ajax.js'
	export default{
		name:'Goodgoods',
		data:function(){
			return{
				commoditydata:[]
			}
		},
		methods:{
			commoditydetailed:function(index){
				//进入商品详情页
				this.$router.push({
						name:'CommoditydetailedPage',
						params:{id:index}
				})
			}
		},
		created:function(){
			Ajax("get","commodity/queryCommoditycollectcount",null,(data)=>{
				this.commoditydata=data
			})
		}
	}
</script>

<style>
	.goodgoods_underline .el-carousel__button{
		display: none;
	}
	.goodgoods_underline .el-carousel__item.is-animating{
		transition: transform 1.5s ease-in-out;
	}
</style>
