import Vue from 'vue'
import VueRouter from 'vue-router'
/* import Home from '../views/Home.vue' */
import HomePage from '../views/HomePage.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'HomePage',
    component: HomePage
  }, 
	{
		path: '/commoditydetailed/:id',
		name: 'CommoditydetailedPage',
		component: ()=>import('../views/CommoditydetailedPage.vue')
	},
	{
		path: '/login',
		name: 'LoginPage',
		component: ()=>import('../views/LoginPage.vue')
	},
	{
		path: '/register',
		name: 'RegisterPage',
		component: ()=>import('../views/RegisterPage.vue')
	},
	{
		path: '/shoppingtrolley',
		name: 'Shoppingtrolley',
		component: ()=>import('../views/ShoppingtrolleyPage.vue')
	},
	{
		path:"/order",
		name:"OrderPage",
		component: ()=>import('../views/OrderPage.vue')
	}
]

const router = new VueRouter({
  routes
})

export default router
